import React from "react";

import Select, { SelectChangeEvent } from "@mui/material/Select";
import {
  Box,
  FormControl,
  IconButton,
  InputLabel,
  MenuItem,
} from "@mui/material";
import { AMM } from "../../../container/amm";

import ArrowDropDownIcon from "@mui/icons-material/ArrowDropDown";
import ArrowDropUpIcon from "@mui/icons-material/ArrowDropUp";

function MarketSelector() {
  const MarketList = ["BTCUSD", "ETHUSD", "BNBUSD", "AVAXUSD"];

  const { marketticker, setMarketTicker } = AMM.useContainer();
  const [open, setOpen] = React.useState(false);

  const handleChange = (event: SelectChangeEvent<typeof marketticker>) => {
    setMarketTicker(event.target.value);
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleOpen = () => {
    setOpen(true);
  };

  return (
    <FormControl>
      <InputLabel
        id="demo-simple-select-label"
        sx={{
          color: "rgb(169, 168, 166)",
          top: "18px",
          left: "56px",
          fontFamily: "Montserrat Light",
          userSelect: "none",
          "&.Mui-focused": { color: "rgb(169, 168, 166)" },
        }}
      >
        Market
      </InputLabel>
      <Select
        labelId="demo-simple-select-label"
        id="demo-simple-select"
        label="Age"
        defaultValue={"ETHUSD"}
        open={open}
        onClose={handleClose}
        onOpen={handleOpen}
        onChange={handleChange}
        inputProps={{ "aria-label": "Without label" }}
        IconComponent={() =>
          open ? <ArrowDropUpIcon /> : <ArrowDropDownIcon />
        }
        sx={{
          width: "220px",
          border: "1px solid rgba(255, 255, 255, 0.12)",
          borderRadius: "10px",
          color: "rgb(236, 232, 227)",
          fontFamily: "Montserrat Medium",
          height: "58px",
          backgroundColor: "rgb(37, 37, 37)",
          "&.Mui-focused .MuiOutlinedInput-notchedOutline": { border: "none" },
          "& .MuiSelect-select": { p: "0 16px", mt: "18px" },
        }}
        MenuProps={{
          PaperProps: {
            sx: {
              bgcolor: "rgb(37, 37, 37)",
              color: "rgb(236, 232, 227)",
              borderRadius: "10px",
              boxSizing: "border-box",
              width: "220px",
              border: "1px solid rgba(255, 255, 255, 0.12)",
              left: "24px !important",
              top: "152px !important",
              "& .MuiList-root": {
                p: "0",
              },
              "& .MuiMenuItem-root": {
                p: "12px 16px",
              },
              "& .MuiMenuItem-root:not(:last-child)": {
                borderBottom: "1px solid rgba(255, 255, 255, 0.12)",
              },
            },
          },
        }}
      >
        {MarketList.map((market) => (
          <MenuItem key={market} value={market} sx={{ color: "white" }}>
            {market}
          </MenuItem>
        ))}
      </Select>
    </FormControl>
  );
}

export default MarketSelector;
